@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  overflow-x: hidden;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  font-weight: 900;
}

h1 {
  font-size: 2.5rem;
  color: #0c62fb;
}

h2 {
  color: #0c62fb;
  font-size: 2.2rem;
}

h3 {
  font-size: 1.5rem;
}

.resaltar-texto {
  margin-top: 0;
  font-size: 4rem;
  font-weight: 700;
  color: 900;
}

.texto-gris {
  color: #626262;
}

.texto-chico {
  font-size: 1.5rem;
  text-align: start;
  color: #333333;
  font-weight: 900;
  margin-bottom: 1rem;
}

.flex {
  display: flex;
}